.calendar-head {
  background: linear-gradient(100deg, lighten($primary, 10%), $primary);
  color: #fff;
  position: relative;
  height: 3rem;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 3rem;
  box-shadow: $shadow-1;
  text-align: center;

  &__button {
    @include button;
  
    position: absolute;
    top: .5rem;
    color: #fff;
    width: 2rem;
    height: 2rem;
    border-radius: $border-radius;
  
    &:first-of-type {
      left: .5rem;
    }
  
    &:nth-of-type(2) {
      left: 3rem;
    }
  
    &:nth-of-type(3) {
      right: 3rem;
    }
  
    &:last-of-type {
      right: .5rem;
    }
  }
}